.user-profile {
 margin: 100px 60px 200px;
 .last-active {
  margin-bottom: 66px;
 }
 .card-partner {
  justify-content: flex-start;
  &__info {
   margin-top: 120px;
   &-name {
    font-size: 70px;
    line-height: 48px;
    margin-bottom: 30px;
   }
   &-wrapper {
    margin-bottom: 70px;
   }
  }
 }
 .ant-btn {
